import {
  syndicateTransport,
  TGetUserSendedSelectionResponse,
  TItemModel,
  userDecisionStorageService,
  UserSwipe
} from 'common';
import { BehaviorSubject } from 'rxjs';
import { TUserChoiseItem } from './user-decision-storage.service';

type TResultsPageData = Record<UserSwipe, Array<TItemModel>>;

type TServiceData = {
  data: TGetUserSendedSelectionResponse | void;
  error: Error | void;
};

class ResultsPageService {
  private static initialData: TServiceData = { data: void 0, error: void 0 };

  data$ = new BehaviorSubject<TServiceData>(ResultsPageService.initialData);

  getData(selectedId: string | null, rejectedId: string | null): void {
    if (!(selectedId && rejectedId)) {
      const data = this.getDataFromStorage();

      this.data$.next({ data, error: void 0 });

      return;
    }

    void syndicateTransport.getUserSendedSelection(selectedId, rejectedId)
      .then((response: TGetUserSendedSelectionResponse) => {
        this.data$.next({ data: response, error: void 0 });
      });
  }

  clearDataFromStorage = (): void => {
    userDecisionStorageService.clear();
    this.data$.next({ data: { [UserSwipe.REJECT]: [], [UserSwipe.SELECT]: [] }, error: void 0 });
  };

  private getDataFromStorage(): TResultsPageData {
    const data = userDecisionStorageService.getItems();

    const result: TResultsPageData = {
      [UserSwipe.SELECT]: [],
      [UserSwipe.REJECT]: []
    };

    data.forEach(({ item, decision }: TUserChoiseItem) => {
      result[decision].push(item);
    });


    return result;
  }
}

export const resultsPageService = new ResultsPageService();
