import { css } from '@emotion/css';
import { Colors } from '../colors';
import { headerMenuHeightPX, mobileMediaQuery } from '../consts';

const desktopHeight = '509px';
const mobileHeight = `calc(100vh - ${headerMenuHeightPX}px)`;

export const bottomSheetStyle = (isShow: boolean): string => css({
  backgroundColor: Colors.BLACK,
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'space-between',
  boxSizing: 'border-box',
  width: '490px',
  height: desktopHeight,
  padding: '30px',
  transition: 'transform 200ms ease-in, opacity 200ms ease-in',
  transform: isShow ? `translate3d(0, -${desktopHeight}, 0)` : `translate3d(0, 0, 0)`,
  opacity: isShow ? 1 : 0,

  [mobileMediaQuery]: {
    height: mobileHeight,
    width: '100%',
    transform: isShow ? `translate3d(0, calc(-100vh + ${headerMenuHeightPX}px), 0)` : `translate3d(0, 0, 0)`
  }
});
