import { BehaviorSubject } from 'rxjs';
import { syndicateTransport, TGetAllProjectsModel, TProjectModel } from '../transport';

export type TProjects = Array<TProjectModel>;

export type TAllProjectsServiceData = {
  data: TProjects | void;
  error: Error | void;
};

class AllProjectsService {
  private cache: TProjects = [];
  private static initialData: TAllProjectsServiceData = { data: void 0, error: void 0 };

  data$ = new BehaviorSubject<TAllProjectsServiceData>(AllProjectsService.initialData);
  hashtags$ = new BehaviorSubject<Array<string>>([]);

  getProjects(): void {
    void syndicateTransport.getAllProjects()
      .then((response: TGetAllProjectsModel) => {
        const tags = Array.from(new Set(response.items.map(({ hashtags }: TProjectModel) => hashtags).flat()));

        this.cache = response.items;
        this.hashtags$.next(tags);
        this.data$.next({ data: response.items, error: void 0 });
      })
      .catch((error: Error) => this.data$.next({ data: void 0, error }));
  }

  filterProjectsByHashtag = (hashtag: string): void => {
    const data = this.cache.filter((project: TProjectModel) => project.hashtags.includes(hashtag));

    this.data$.next({ data, error: void 0 });
  };

  resetFilter = (): void => {
    this.data$.next({ data: this.cache, error: void 0 });
  };
}

export const allProjectsService = new AllProjectsService();
