import React from 'react';
import { projectDetailedStyle, projectImgStyle, projectItemStyle } from './project-item.style';
import { Detailled } from '../detailed';

type TProjectItemProps = {
  onOpenDetailedInfo: (id: number) => void;
  imageUrl: string;
  projectName: string;
  id: number;
};

export function ProjectItem({ onOpenDetailedInfo, imageUrl, projectName, id }: TProjectItemProps): JSX.Element {
  const handleOpenDetailedInfo = React.useCallback(() => {
    onOpenDetailedInfo(id);
  }, [id, onOpenDetailedInfo]);

  return (
    <div className={projectItemStyle}>
      <img className={projectImgStyle} src={imageUrl} alt={projectName} />
      <div className={projectDetailedStyle}>
        <Detailled onOpenDetailedInfo={handleOpenDetailedInfo} projectName={projectName} />
      </div>
    </div>
  );
}
