import React from 'react';
import { hashtagListStyle } from './hashtag-list.style';
import { Text } from '../text';

type THashtagListProps = {
  hasthags: Array<string>;
  onClickHashtag: (hashtag: string) => void;
  onReset: () => void;
};

type THashtagProps = {
  hashtag: string;
  isActive: boolean;
  onClick: (hashtag: string) => void;
};

const resetHashtag = '#ALLPROJECTS';

function Hashtag({ hashtag, isActive, onClick }: THashtagProps): JSX.Element {
  const handleClick = React.useCallback(() => onClick(hashtag), [onClick, hashtag]);

  return (
    <Text align="center" onClick={handleClick} size="l" color={isActive ? 'red' : 'white'}>{hashtag}</Text>
  );
}

export function HashtagList({ hasthags, onClickHashtag, onReset }: THashtagListProps): JSX.Element {
  const [activeHashtag, setActiveHashtag] = React.useState(resetHashtag);

  const handleClickHashTag = React.useCallback((hashtag: string) => {
    setActiveHashtag(hashtag);

    if (hashtag === resetHashtag) {
      onReset();

      return;
    }
    onClickHashtag(hashtag);
  }, [setActiveHashtag, onClickHashtag, onReset]);

  return (
    <div className={hashtagListStyle}>
      {[resetHashtag, ...hasthags].map((hashtag: string) => (
        <Hashtag
          key={hashtag}
          hashtag={hashtag}
          isActive={hashtag === activeHashtag}
          onClick={handleClickHashTag}
        />)
      )}
    </div>
  );
}
