import { bottomSheetService, TGetUserSendedSelectionResponse, TItemModel, UserSwipe } from 'common';
import { CopyRight, HeaderMenu, Layout, ProjectItem } from 'components';
import React from 'react';
import { ResultSwitcher } from './result-switcher';
import { ResultsMenuSheet } from './results-menu-sheet';
import { resultItemWrapperStyle, resultsPageStyle, resultSwitcherWrapperStyle } from './results-page.style';

type TResultsPageContentProps = {
  data: TGetUserSendedSelectionResponse;
  onClear: VoidFunction | undefined;
};

export function ResultsPageContent({ data, onClear }: TResultsPageContentProps): JSX.Element {
  const [decisionView, setDecisionView] = React.useState(UserSwipe.SELECT);
  const selectedCount = data[UserSwipe.SELECT].length;
  const rejectedCount = data[UserSwipe.REJECT].length;

  const handleSetDecisionView = React.useCallback((state: UserSwipe) => setDecisionView(state), []);

  const handleOpenResultMenuSheet = React.useCallback(() => bottomSheetService.open(
    <ResultsMenuSheet
      selectedCount={selectedCount}
      rejectedCount={rejectedCount}
      onSwitch={handleSetDecisionView}
      onClear={onClear}
    />
  ), [handleSetDecisionView, selectedCount, rejectedCount, onClear]);

  return (
    <Layout
      headerContent={<HeaderMenu />}
      footerContent={<CopyRight onClick={handleOpenResultMenuSheet} />}
      disablePaddings
    >
      <div className={resultsPageStyle}>
        {data[decisionView].map((project: TItemModel, index: number) => (
          <div key={index} className={resultItemWrapperStyle}>
            <ProjectItem
              id={project.project}
              projectName={project.projectName}
              imageUrl={project.photo.url}
              onOpenDetailedInfo={() => void 0}
            />
          </div>
        )
        )}
        <div className={resultSwitcherWrapperStyle}>
          <ResultSwitcher
            onSwitch={handleSetDecisionView}
            selectedCount={selectedCount}
            rejectedCount={rejectedCount}
          />
        </div>
      </div>
    </Layout>
  );
}
