import { UserSwipe } from 'common';
import { Text } from 'components';
import React from 'react';
import { resultSwitcherStyle } from './results-page.style';

type TResultSwitcherProps = {
  onSwitch: (state: UserSwipe) => void;
  selectedCount: number;
  rejectedCount: number;
};

export function ResultSwitcher({
  onSwitch,
  selectedCount,
  rejectedCount
}: TResultSwitcherProps): JSX.Element {
  const [state, setState] = React.useState(UserSwipe.SELECT);

  const handleSetSelectedDecisionView = React.useCallback(() => {
    setState(UserSwipe.SELECT);
    onSwitch(UserSwipe.SELECT);
  }, [onSwitch]);

  const handleSetRejectedDecisionView = React.useCallback(() => {
    setState(UserSwipe.REJECT);
    onSwitch(UserSwipe.REJECT);
  }, [onSwitch]);

  return (
    <div className={resultSwitcherStyle}>
      <Text
        align="center"
        onClick={handleSetSelectedDecisionView}
        color={state === UserSwipe.SELECT ? 'red' : 'white'}
      >
        your likes {selectedCount}
      </Text>
      <Text
        align="center"
        onClick={handleSetRejectedDecisionView}
        color={state === UserSwipe.REJECT ? 'red' : 'white'}
      >
        your secret desires {rejectedCount}
      </Text>
    </div>
  );
}
