import { css } from '@emotion/css';
import { Colors } from '../colors';
import { copyrightHeightPX, generalMenuWidthPX } from '../consts';

export const copyrightStyle = (hasOnClick: boolean): string => css({
  boxSizing: 'border-box',
  cursor: hasOnClick ? 'pointer' : 'auto',
  height: copyrightHeightPX,
  overflow: 'hidden'
});

export const copyrightBlockStyle = css({
  display: 'flex',
  justifyContent: 'center',
  flexDirection: 'column',
  alignItems: 'center',
  width: `${generalMenuWidthPX}px`,
  backgroundColor: Colors.BLACK,
  height: '68px'
});

