import { BehaviorSubject } from 'rxjs';

export type TBottomSheetState = {
  isShow: boolean;
  component: JSX.Element | null;
};

class BottomSheetService {
  state$ = new BehaviorSubject<TBottomSheetState>({ isShow: false, component: null });

  open(component: JSX.Element): void {
    this.state$.next({ isShow: true, component });
  }

  close = (): void => {
    this.state$.next({ isShow: false, component: this.state$.getValue().component });
  };
}

export const bottomSheetService = new BottomSheetService();
