import { bottomSheetService, useRx } from 'common';
import { SheetContainer } from 'components/sheet-container';
import React from 'react';
import { bottomSheetStyle } from './bottom-sheet.style';
import { Text } from '../text';

export function BottomSheet(): JSX.Element {
  const state = useRx(bottomSheetService.state$);

  return (
    <SheetContainer bottom>
      <div className={bottomSheetStyle(state.isShow)}>
        {state.component}
        <Text align="center" color="white" onClick={bottomSheetService.close}>close</Text>
      </div>
    </SheetContainer>
  );
}
