import { allProjectsService, useRx } from 'common';
import { HashtagList } from 'components';
import React from 'react';

export function HashtagsSheet(): JSX.Element {
  const hastags = useRx(allProjectsService.hashtags$);

  return (
    <HashtagList
      hasthags={hastags}
      onClickHashtag={allProjectsService.filterProjectsByHashtag}
      onReset={allProjectsService.resetFilter}
    />
  );
}
