import React from 'react';
import { sheetContainerStyle } from './sheet-container.style';

type TSheetContainerProps = {
  top?: boolean;
  bottom?: boolean;
  children: React.ReactNode;
};

export function SheetContainer({ bottom, top, children }: TSheetContainerProps): JSX.Element {
  return (
    <div className={sheetContainerStyle(top, bottom)}>
      {children}
    </div>
  );
}
